import React from "react";
import Header from "./commonComponent/header";
import SideBar from "./commonComponent/sideBar";
import { Link } from "react-router-dom";

function EditAdvertisement() {
  return (
    <>
      <Header />

      <div className="admin_main">
        <SideBar />

        <div className="admin_contentpart">
          <Link to="/admin/advertisement-package" className="comman_btn w-fit">
            <i className="fa fa-arrow-left" />
          </Link>
          <div className="row comman_design mx-0 mt-4">
            <div className="col-12">
              <div className="row comman_table pt-md-4 pb-md-4 pb-3 px-md-4 px-2">
                <h3 className="mb-3 fs-5 fw-semibold">
                  Add Advertisement Package
                </h3>
                <div className="form-design">
                  <div className="row">
                    <div className="col-md-4">
                      <div className="form-group">
                        <label htmlFor="form-label">PACKAGE NAME</label>
                        <input type="text" className="form-control" />
                      </div>
                    </div>
                    <div className="col-md-4">
                      <div className="form-group">
                        <label htmlFor="form-label">PRICE</label>
                        <input type="text" className="form-control" />
                      </div>
                    </div>
                    <div className="col-md-4">
                      <div className="form-group">
                        <label htmlFor="form-label">DURATION</label>
                        <input type="text" className="form-control" />
                      </div>
                    </div>
                    <div className="col-12">
                      <button className="comman_btn w-fit">Add Package</button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default EditAdvertisement;
